<template>

<div class="section photo">
  <div id="photoTitle">
    <SectionTitle title="Foto"></SectionTitle>
  </div>
  <div class="center">
    <ImgBox3></ImgBox3>
    <ImgBox3></ImgBox3>
    <ImgBox3></ImgBox3>
    <ImgBox3></ImgBox3>
    <ImgBox3></ImgBox3>
    <ImgBox3></ImgBox3>
  </div>
  <div id="photoButton">
    <MainButton link="/hochzeitsfotografie" button-text="Zeig mir mehr"></MainButton>
  </div>
</div>

</template>

<script>

import MainButton from "@/components/buttons/MainButton";
import SectionTitle from "@/components/titles/SectionTitle";
import ImgBox3 from "@/components/imgBox/ImgBox3";

export default {
  name: "SectionPhoto",
  data() {
    return {
      link: "background-image: url('/img/bg-meteor.svg');",
    }
  },
  components: {
    ImgBox3,
    SectionTitle,
    MainButton
  },
  mounted() {
    /* EDIT SLIDER SETTINGS HERE */
    /* eslint-env jquery */
    $(document).ready(() => {

      $('.center').slick({
        dots: true,
        arrows: true,
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 3,
        touchThreshold: 50,
        responsive: [
          {
            breakpoint: 1201,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              infinite: true,
              dots: true,
              arrows: false,
            }
          },
          {
            breakpoint: 601,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
              arrows: false,
            }
          },
        ]
      });
    });
  }
}

</script>


<style>

.slick-slide {
  margin: 0;
}

.slick-list {
  height: max-content;
  /*height: calc(var(--vh, 1vh) * 100);*/
}

.slick-prev:before {
  content: "<";
  color: black;
  font-size: 30px;
}

.slick-next:before {
  content: ">";
  color: black;
  font-size: 30px;
}

.slick-dots {
  display: flex;
  justify-content: center;
  margin: 0;
  list-style-type: none;
}

.slick-dots li {
  margin: 0;
  padding: 0;
  height: max-content;

}

.slick-dots li button:before {
  content: "⬤";
  opacity: 0.4;
}

.slick-dots li.slick-active button {
  opacity: 1;
}

</style>


<style scoped>


.center {
  display: flex;
  align-items: center;
  width: 90vw;
  margin: 0 auto;
}

.photo {
  margin: 0;

}

#photoTitle {
  margin-bottom: 20px;
}

#photoButton {
  margin: 50px 0;
}

</style>